<template>
	<view>
		<map @markertap="goto" class="map" :markers="cover" :longitude="centerlog" :latitude="centerlat"></map>
		<!-- <button class="btn-long" @click="doNavi()">导航</button> -->
	</view>
</template>

<script>
	import QQMapWx from '@/utils/qqmap-wx-jssdk.js'
	export default {
		name: 'Navigation',
		props: {
			initialLatitude: {
				type: String,
				default: '23.10647'
			},
			initialLongitude: {
				type: String,
				default: '113.32446'
			},

		},
		data() {
			return {
				//中心维度
				centerlat: this.initialLatitude,
				centerlog: this.initialLongitude,
				//路径规划开始维度
				startlat: '',
				startlog: '',
				//路径规划结束维度
				endlat: '',
				endlog: '',
				name: '',
				qqmapsdk: '',
				//标注点
				cover: []
			}
		},
		onLoad: function() {
			this.qqmapsdk = new QQMapWx({
				key: '3XWBZ-6KC67-ULWXK-HUTAN-W6SJK-JLBTW'
			});

		},
		onShow: function() {
			//调用接口
			this.qqmapsdk.search({
				keyword: '酒店',
				success: function(res) {
					console.log(res);
				},
				fail: function(res) {
					console.log(res);
				},
				complete: function(res) {
					console.log(res);
				}
			});
			this.getCover();

			this.getLocation();
		},
		methods: {
			goto() {
				let plugin = requirePlugin('routePlan');
				let key = '3XWBZ-6KC67-ULWXK-HUTAN-W6SJK-JLBTW'; //使用在腾讯位置服务申请的key
				let referer = 'map'; //调用插件的app的名称
				let startPoint = JSON.stringify({ //终点
					'name': '我的位置',
					latitude: this.startlat,
					longitude: this.startlog
				});
				let endPoint = JSON.stringify({ //终点
					name: this.name,
					latitude: this.endlat,
					longitude: this.endlog
				});
				uni.navigateTo({
					url: 'plugin://routePlan/index?key=' + key + '&referer=' + referer + '&endPoint=' + endPoint +
						'&startPoint=' + startPoint + '&navigation=1',
					success(res) {
						console.log("res==1111=", res)
					},
					fail(res) {
						console.log("res==2222=", res)
					},
				});
			},
			async getCover() {
				let params = {
					page: 1,
					limit: 10
				}
				let res = await this.$api.list(`marker`, params);
				// 循环遍历获取的marker数据
				res.data.marker.forEach(marker => {
					// 添加marker的经纬度信息到cover中
					this.cover.push({
						longitude: marker.longitude,
						latitude: marker.latitude,
						// 如果marker中有name字段，也添加到cover中
						name: marker.name,
						// 如果marker中有iconURL字段，也添加到cover中
						iconURL: marker.iconURL,
						// 如果marker中有气泡内容字段，也添加到cover中
						callout: {
							content: `${marker.title}\n[img]${marker.imageUrl}[/img]`,
							bgColor: '#ffffff',
							padding: 10,
							borderRadius: 5,
							display: 'BYCLICK',
							textAlign: 'center'
						}
						// 继续添加其他需要的字段...
					});
				});
				console.log('cover', this.cover)

			},
			markertap(e) {
				console.log(e)
				var clickmarkerId = e.mp.detail.markerId
				//根据e.mp.detail.markerId跟撒点的id进行判断，如果相等就进行赋值
				for (var i = 0; i < this.marker.length; i++) {
					if (this.marker[i].id == clickmarkerId) {
						this.endlog = this.marker[i].longitude
						this.endlat = this.marker[i].latitude
						this.name = this.marker[i].title
						/* this.sendaddress = this.marker[i].address */
					}
				}
			},

			getLocation() {
				let that = this
				// 获取用户是否开启 授权获取当前的地理位置、速度的权限。
				uni.getSetting({
					success(res) {
						console.log(res)

						// 如果没有授权
						if (!res.authSetting['scope.userLocation']) {
							// 则拉起授权窗口
							uni.authorize({
								scope: 'scope.userLocation',
								success() {
									//点击允许后--就一直会进入成功授权的回调 就可以使用获取的方法了
									uni.getLocation({
										type: 'wgs84',
										success: function(res) {
											that.startlog = res.longitude
											that.startlat = res.latitude
											console.log(res)
											console.log('当前位置的经度：' + res.longitude)
											console.log('当前位置的纬度：' + res.latitude)
											uni.showToast({
												title: '当前位置的经纬度：' + res.longitude +
													',' + res.latitude,
												icon: 'success',
												mask: true
											})
										},
										fail(error) {
											console.log('失败', error)
										}
									})
								},
								fail(error) {
									//点击了拒绝授权后--就一直会进入失败回调函数--此时就可以在这里重新拉起授权窗口
									console.log('拒绝授权', error)

									uni.showModal({
										title: '提示',
										content: '若点击不授权，将无法使用位置功能',
										cancelText: '不授权',
										cancelColor: '#999',
										confirmText: '授权',
										confirmColor: '#f94218',
										success(res) {
											console.log(res)
											if (res.confirm) {
												// 选择弹框内授权
												uni.openSetting({
													success(res) {
														console.log(res.authSetting)
													}
												})
											} else if (res.cancel) {
												// 选择弹框内 不授权
												console.log('用户点击不授权')
											}
										}
									})
								}
							})
						} else {
							// 有权限则直接获取
							uni.getLocation({
								type: 'wgs84',
								success: function(res) {
									that.startlog = res.longitude
									that.startlat = res.latitude
									console.log(res)
									console.log('当前位置的经度：' + res.longitude)
									console.log('当前位置的纬度：' + res.latitude)
									uni.showToast({
										title: '当前位置的经纬度：' + res.longitude + ',' + res
											.latitude,
										icon: 'success',
										mask: true
									})
								},
								fail(error) {
									uni.showToast({
										title: '请勿频繁调用！',
										icon: 'none',
									})
									console.log('失败', error)
								}
							})
						}
					}
				})

			}

		}
	}
</script>

<style lang="scss" scoped>
	.map {
		width: 100vw;
		height: 100vh;
	}
</style>